.catalog-card__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 61px 72px;
  box-sizing: border-box;
  background-color: $color-default-white;
  border: 7px solid $color-default-white;
  min-height: 448px;

  @include vp-1200 {
    padding: 58px 110px 62px;
  }

  @include vp-767 {
    min-height: 384px;
    padding: 44px 36px 49px;
  }

  &:hover,
  &:focus {
    outline: none;
    border-image: $gradient-primary;
    border-image-slice: 1;
  }

  &:hover .catalog-card__button:not(:disabled),
  &:focus .catalog-card__button:not(:disabled) {
    outline: none;
    color: $color-default-white;
    background: $gradient-primary;
  }

  h4 {
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
    color: $color-primary;
    font-size: 26px;
    font-family: $font-family-secondary;
    font-weight: 700;
    line-height: 100%;

    @include vp-767 {
      font-size: 22px;
    }
  }

  span {
    color: #495a7d;
    font-size: 16px;
    font-family: $font-family-secondary;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    margin-bottom: 40px;
  }
}

.catalog-card__price-container {
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-end;
  margin-top: auto;
  margin-bottom: 50px;
  position: relative;

  @include vp-767 {
    margin-bottom: 44px;
  }

  p {
    padding: 0;
    margin: 0;
    color: #103aae;
    font-size: 60px;
    font-family: $font-family-secondary;
    font-weight: 700;
    line-height: 100%;
    margin-right: 14px;
    z-index: 1;

    @include vp-767 {
      font-size: 48px;
      margin-right: 10px;
    }
  }

  b {
    padding: 0;
    margin: 0;
    position: absolute;
    top: -44px;
    left: -19px;
    color: #e8e8e8;
    font-size: 100px;
    font-family: $font-family-secondary;
    font-style: normal;
    font-weight: 700;
    line-height: 100%;
    opacity: 0.5;
    z-index: 0;

    @include vp-767 {
      top: -34px;
      left: -12px;
      font-size: 80px;
    }
  }

  svg {
    z-index: 3;
    fill: $color-primary;
    position: relative;
    top: -8px;

    @include vp-767 {
      width: 28px;
      height: 35px;
    }
  }
}

.catalog-card__button {
  padding: 25px 42px;
}
